import { useContext, useEffect, useRef } from "react"
import Context from "../../../Context/Context"
import React from "react"
import getPosts from "../../../hooks/getPosts"
import { Link } from "react-router-dom"
import {useDispatch} from 'react-redux';
import { useNavigate } from "react-router-dom"
import './login.css'

function BioLogin(date){
    const dispatch = useDispatch()
    let navigate = useNavigate()
    let email = useRef()
    let pass = useRef()
    const {usersDate} = useContext(Context)
    let trueHref = window.screen.width > 500 ? '/general' : '/bio'

    ///////////////////
    let timer1

    let timer2
    /////////////////////

    function Reg(){
        usersDate.forEach(item=>{
            if(email.current.value == item.email && pass.current.value == item.password){

                dispatch({type:'SET_IR'})
                dispatch({type: 'SET_C_U', item: item})

                getPosts(commit)

                navigate('/general')
                return false
            }
            if(pass.current.value != item.password)missedP()
            if(email.current.value != item.email)missedE()
        })
    }

    function missedE(){
        email.current.style.borderBottom = '3px solid red'
        email.current.value = ''
        timer1 = setTimeout(()=>{
            email.current.style.borderBottom = '0.5px solid rgba(0, 0, 0, 0.24)'
            
        }, 2000)
        clearTimeout(timer1)
    }
    function missedP(){
        pass.current.style.borderBottom = '3px solid red'
        pass.current.value = ''
        timer2 = setTimeout(()=>{
            pass.current.style.borderBottom = '0.5px solid rgba(0, 0, 0, 0.24)'
            
        }, 2000)
        clearTimeout(timer2)
    }


    function commit(arg){
        dispatch({type: 'SET_P', item: arg})
        dispatch({type: 'SET_I_P'})
    }
    return(
        <div className="container">
        <h2 className="login_lable">Log in</h2>
        <input ref={email} type="text" className="login_in bio_inputs" placeholder="Login"/>
        <input ref={pass} type="text" className="password_in bio_inputs" placeholder="Password"/>
        <div className="reg_active" onClick={()=>date.setRegister(true)}>Create account</div>
        
            <div to={trueHref} className="log_but" onClick={()=> Reg()}>Enter</div>
        
    </div>
    )
}
export default BioLogin